    replace(pageName,params) {
        //Replace the current scene with a new route
        // this.pageWillDisappear && this.pageWillDisappear();
        let router=this._getRouterFromName(pageName);
        if(params){
            router.params = Object.assign({},params);
        }
        this.loadView(router,'pop');
        // this.pageDidDisappear && this.pageDidDisappear();
    }

    replaceAtIndex(pageName,index,cb,params) {
        //  Replace a scene as specified by an index
        let routerStack=this.getCurrentRoutes(),routerCount=routerStack.length;

        if(index<routerCount){
            routerStack=this.routeHistoryStack.slice(0,index);
            this.routeHistoryStack=routerStack;
            this.replace(pageName,params);
            cb&&cb();
        }
    }

    replacePrevious(pageName,params) {
        //Replace the previous scene
        let routerStack=this.getCurrentRoutes(),routerCount=routerStack.length;
        this.replaceAtIndex(pageName,routerCount-2,null,params);
    }

    _getRouteName(pageName){
        if(!pageName){return ''}
        if (global.cw_routeMapConfig) {
            if (global.cw_routeMapConfig[pageName]) {
                pageName = global.cw_routeMapConfig[pageName];
            }
        }
        return pageName;
    }
    _getRouterFromName(pageName){
        let route=this._getRouteName(pageName);
        if(route){
            if(route.trim().indexOf("/")!=0){
                route="/"+route;
            }
            let router=this._routerMaps[route]||{};
            return router;
        }else{
            throw new Error('invalid pageName,pageName does not in routeStack');
        }

    }

    getCurrentRoutes() {
        if(this.routeHistoryStack){
            return this.routeHistoryStack;
        }
    }

    jumpBack() {
        //- Jump backward without unmounting the current scene
    }

    jumpTo() {
        //Transition to an existing scene without unmounting
    }

