    static FloatFromRight = {"gestures": {"jumpForward": {}, "jumpBack": {},"animate":"sliderightin"}};
    static FloatFromLeft = {"gestures": {"jumpForward": {}, "jumpBack": {},"animate":"slideleftin"}};
