    static FadeAndroid = {"gestures": {"jumpForward": {}, "jumpBack": {},"animate":"slidedownin"}};
    static HorizontalSwipeJump = {"gestures": {"jumpForward": {}, "jumpBack": {},"animate":"slideleftin"}};
    static HorizontalSwipeJumpFromRight = {"gestures": {"jumpForward": {}, "jumpBack": {},"animate":"sliderightin"}};
